<template>
  <div>
    <title-bar :title-stack="titleStack()" />
    <section class="section is-main-section">
      <b-tabs type="is-boxed" class="margin-bottom-075">
        <b-tab-item :label="defaultname" icon="laptop" :disabled="true">
          <project-task :data="alltasks"></project-task>
        </b-tab-item>
      </b-tabs>
    </section>
  </div>
</template>

<script>
import TitleBar from '@/components/TitleBar'
import ProjectTask from '@/components/Article/ProjectTask'

export default {
  name: 'FirstProject',
  components: {
    TitleBar,
    ProjectTask
  },
  data() {
    return {
      alltasks: [],
      /**
       * 默认展示项目
       */
      defalutprojs: [],
      defaultname: ''
    }
  },
  computed: {
    listData() {
      return this.$store.state.twoproject.xdefaulttwoprojs
    }
  },
  watch: {
    listData(newVal) {
      // console.log('First 变动')
      // 数据变化后，调用b组件内的函数
      this.defalutprojs = newVal
      this.titleStack()
    }
  },
  mounted() {
    this.defalutprojs = this.$store.state.twoproject.defaulttwoprojs
    this.titleStack()
    this.init()
  },

  methods: {
    /**
     * 初始化
     */
    init() {
      for (let i = 0; i < 10; i++) {
        const task = {}
        const relatedtask = {
          createdate: '',
          modifydate: '',
          assigner: `冯涛`,
          createautor: `居溱`,
          modifyautor: '',
          counter: i * 8 + Math.round((Math.random() * 100) / 100),
          usefilename: '',
          case: [],
          us: [],
          device: [],
          configjson: '',
          reviewautor: '新评审人员',
          reviewstatus: 'need review',
          taskid: `12345678` + i,
          taskname: `Task000` + i + `_MTStar_US00` + i + `_自动化测试`,
          tasktype: 'Debug',
          taskstatus: 'Active'
        }
        task.relatedtask = relatedtask
        this.alltasks.push(task)
      }
      // console.log(this.alltasks)
    },
    /**
     * 返回array
     * title使用
     */
    titleStack() {
      if (this.defalutprojs.length === 0) {
        this.defaultname = '预展示第二个项目'
      } else {
        this.defaultname = this.defalutprojs[1].name
      }
      return ['RoWalock', this.defaultname]
    }
  }
}
</script>

<style scoped>
.margin-bottom-075 >>> nav {
  margin-bottom: 0.75rem;
}
</style>
