<template>
  <section>
    <div v-if="taskdata.length > 0">
      <div v-for="(item, index) in taskdata" :key="index">
        <article class="media">
          <figure class="media-left">
            <b-tag type="is-link">{{ item.relatedtask.tasktype }}</b-tag>
          </figure>
          <div class="media-content">
            <div class="columns">
              <div class="column is-12">
                <div class="columns">
                  <div class="column is-4">
                    <strong :class="[{ 'has-text-grey': item.relatedtask.taskstatus != 'Active' }]">
                      {{ item.relatedtask.taskname }}
                    </strong>
                  </div>
                  <div class="column is-3">
                    <b-taglist>
                      <b-tag
                        type="is-light"
                        :class="[
                          { 'has-text-danger has-text-weight-bold': item.relatedtask.taskstatus === 'Block' },
                          { 'has-text-link has-text-weight-bold': item.relatedtask.taskstatus != 'Block' }
                        ]"
                      >
                        {{ item.relatedtask.taskstatus }}
                      </b-tag>
                      <b-tag type="is-light" class="has-text-weight-semibold">@{{ item.relatedtask.assigner }}</b-tag>
                      <b-field type="is-danger">
                        <b-select
                          v-if="isassignto === index"
                          icon="account"
                          size="is-small"
                          class="mb-2"
                          @change.native="changeAssignTo(index, $event)"
                        >
                          <option v-for="option in allusers" :key="option" :value="option">
                            {{ option }}
                          </option>
                        </b-select>
                      </b-field>
                    </b-taglist>
                  </div>
                  <div class="column">
                    <div class="level">
                      <div class="level-left"></div>
                      <div class="level-right">
                        <a class="level-item" @click="assignTo(index)">
                          <b-icon icon="reply"></b-icon>
                        </a>
                        <a class="level-item" @click="deleteTo(index, item, taskdata)">
                          <b-icon icon="delete-variant"></b-icon>
                        </a>
                        <a class="level-item" @click="shutdownTo(index)">
                          <b-icon icon="power"></b-icon>
                        </a>
                        <a class="level-item" @click="freezeTo(index)">
                          <b-icon icon="link-lock"></b-icon>
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="columns">
                  <div class="column is-5">
                    <b-progress
                      class="margin-Z1"
                      type="is-cgreen-3"
                      :value="item.relatedtask.counter"
                      :show-value="false"
                      size="is-small"
                    ></b-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </article>
        <hr class="margin-hr" />
      </div>
    </div>
    <delete-modal ref="showmodal" @getArrayValue="getArrayValue" />
  </section>
</template>

<script>
import DeleteModal from '@/components/Modal/DeleteModal'

export default {
  /**
   * OBSOLETE: 这个已经不用了
   */
  name: 'ProjectTask',
  components: {
    DeleteModal
  },
  props: {
    /**
     * 需要展示的所有数据
     */
    data: Array,
    /**
     * 每页数量
     */
    prepage: {
      type: Number,
      default: 5
    },
    /**
     * 显示删除icon
     */
    showdelicon: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      taskdata: [],

      // 重新指派
      isassignto: -1,
      allusers: ['居溱', '刘凯', '冯涛', '李荧辉']
    }
  },
  mounted() {
    this.init()
  },

  methods: {
    init() {
      this.taskdata = this.data
    },

    /**
     * 重新指派任务
     */
    assignTo(index) {
      this.isassignto = index
    },
    changeAssignTo(index, e) {
      this.taskdata[index].relatedtask.assigner = e.target.value
      this.isassignto = -1
      // console.log(this.taskdata[index])
    },
    /**
     * 删除对应任务的真实操作
     */
    deleteTo(index, item, data) {
      this.$refs.showmodal.open(index, data)
    },
    /**
     * 关闭任务
     */
    shutdownTo(index) {
      // 关闭任务, 该任务状态从active 变为done
      const statusvalue = this.taskdata[index].relatedtask.taskstatus
      if (statusvalue === 'Active') {
        this.taskdata[index].relatedtask.taskstatus = 'Done'
      } else {
        this.taskdata[index].relatedtask.taskstatus = 'Active'
      }
      this.$buefy.toast.open({
        duration: 1500,
        message: `任务状态已更改!`,
        position: 'is-top',
        type: 'is-link'
      })
    },
    /**
     * 冻结任务
     * 如果冻结,暂时不应该能有操作
     */
    freezeTo(index) {
      this.taskdata[index].relatedtask.taskstatus = 'Block'
    },

    /**
     * 获取新的数组数据
     */
    getArrayValue(rearray) {
      this.taskdata = rearray
    }
  }
}
</script>

<style scoped>
.margin-hr {
  margin: 0.5rem 0 0.5rem 0;
}

.margin-Z1 {
  margin: -1rem;
}
</style>
